<template>
	<el-row>
		<div style="position: fixed; bottom: 0; right: 0; background-color: red; color: #fff;">{{ width }}</div>
		<el-col class="sidebar-box" :class="{'sidebar-show': sidebarShow}" :xs="xs" :sm="sm" :md="md">
			<sidebar @sidebarHide="sidebarHide"></sidebar>
		</el-col>
		<el-col :xs="{span: 24, offset: 0}" :sm="{span: 24 - sm, offset: sm}" :md="{span: 24 - md, offset: md}">
			<el-container>
				<el-header>
					<vHeader></vHeader>
				</el-header>
				<el-main id="content" ref="content">
					<router-view/>
				</el-main>
			</el-container>
		</el-col>
	</el-row>
</template>

<script>
import Hammer from 'hammerjs'
import vHeader from './components/v-header'
import sidebar from './components/sidebar'
export default {
	name: 'App',
	components: {
		vHeader,
		sidebar,
	},
	data() {
		return {
			xs: 14,
			sm: 6,
			md: 4,
			sidebarShow: true,
			width: '',
		}
	},
	beforeCreate() {
	},
	created() {
		let screen = document.body.clientWidth
		if (screen < 768) {
			this.$store.commit('device', 'mobile')
		}
		else if (screen >= 768) {
			this.$store.commit('device', 'pc')
		}
		window.onresize = () => {
			console.log(document.body.clientWidth)
			this.width = document.body.clientWidth
		}
	},
	mounted() {
		let device = this.device
		if (device === 'mobile') {
			let content = this.$refs.content.$el
			let mc = new Hammer(content)
			mc.on("swiperight", e => {
				this.sidebarShow = true
			})
		}
	},
	computed: {
		device() {
			return this.$store.state.device
		},
	},
	methods: {
		sidebarHide() {
			this.sidebarShow = false
		},
	},
}
</script>

<style lang="less">
	@import "common/style/common";
	html, body {
		width: 100%;
		overflow-x: hidden;
	}
	#app {
		.sidebar-box {
			position: fixed;
			height: 100%;
			transform: translateX(-100%);
			transition: transform 0.3s ease;

			&.sidebar-show {
				transform: translateX(0);
			}
		}

		@import "./common/style/docs";
	}
</style>



