import Vue from 'vue'
import Router from 'vue-router'
import App from './App'

Vue.use(Router)

export default new Router({
	// mode: 'history',
	routes: [
		{
			path: '/',
			component: App,
			redirect: '/docs',
			children: [
				{
					path: '/docs',
					name: 'note',
					component: resolve => {
						require.ensure(['./pages/docs'], () => {
							resolve(require('./pages/docs'))
						})
					},
				},
				{
					path: '/docs/:dir',
					name: 'docs',
					component: resolve => {
						require.ensure(['./pages/docs'], () => {
							resolve(require('./pages/docs'))
						})
					},
				},
			],
		},
	]
})



