<template>
	<div
		class="sidebar"
		ref="sidebar"
		:class="{mouseleave: isMouseLeave}"
		@mouseenter="mouseenter"
		@mouseleave="mouseleave"
	>
		<el-menu
			:default-active="this.$route.path"
			active-text-color="#409eff"
			@open="handleOpen"
			@close="handleClose"
			@select="select"
			:router="true"
			style="min-height: 100%;"
		>
			<el-submenu v-for="(menu, index) in navs" :index="index + ''" :key="menu.name">
				<template slot="title">
					<span>{{ menu.name }}</span>
				</template>
				<el-menu-item v-for="(item) in menu.submenus" :index="`/docs/${encodeURIComponent(menu.name + '/' + item.router)}`" :key="item.value">{{ item.value }}</el-menu-item>
			</el-submenu>
		</el-menu>
	</div>
</template>

<script>
import Hammer from 'hammerjs'
export default {
	name: "sidebar",
	data() {
		return {
			isMouseLeave: false,
			navs: '',
		}
	},
	beforeCreate() {
	},
	created() {
		this.getMenuJson('navs.json')
	},
	mounted() {
		let device = this.device
		if (device === 'mobile') {
			let sidebar = this.$refs.sidebar
			let mc = new Hammer(sidebar)
			mc.on("swipeleft", e => {
				this.$emit('sidebarHide')
			})
		}
	},
	watch: {},
	computed: {
		device() {
			return this.$store.state.device
		},
	},
	methods: {
		filterLi(str) {
			let patten = /\d+\. ?\[.+?\]\(#.+?\)/g
			let list = str.match(patten)
			return list
		},
		processLi(str) {
			// "1. [ECMAScript 6简介](#docs/intro)"
			let patten = /\[(.*?)\]\(#(.*?)\)/
			let result = str.match(patten)
			if (result) {
				result = {
					router: result[2],
					value: result[1],
				}
			}
			return result
		},
		getMenuJson(menu) {
			this.$axios.get(`./static/${menu}`)
				.then(res => {
					this.processMenu(res.data)
				})
		},
		processMenu(data) {
			Promise.all(data
				.filter(item => item.type === 'folder')
				.map(item => {
					return this.$axios.get(`./static/docs/${item.value}/sidebar.md`)
						.then(res => {
							return {
								menu: item.value,
								submenus: res
							}
						}, () => {
							// return this.getMenuJson(`./static/docs/${item.value}/childMenu.json`)
							return 'err'
						})
				}))
				.then(arr => {
					arr = arr
						.filter(item => {
							return typeof item === 'object'
						})
						.map(item => {
							let list = this
								.filterLi(item.submenus.data)
								.map(item => {
									return this.processLi(item)
								})
							return {
								name: item.menu,
								submenus: list,
							}
						})
					this.navs = arr
				})
		},
		handleOpen(key, keyPath) {
			// console.log(key, keyPath)
		},
		handleClose(key, keyPath) {
			// console.log(key, keyPath)
		},
		select(key, keyPath) {
			// console.log(key, keyPath)
			this.$store.commit('activeNav', key)
		},
		mouseenter(event) {
			this.isMouseLeave = false
		},
		mouseleave(event) {
			this.isMouseLeave = true
		},
	},
}
</script>

<style lang="less" scoped>
	.sidebar {
		height: 100%;
		@media (max-width: 768px) {
			overflow-y: auto;
		}
		@media (min-width: 768px) {
			overflow-y: auto;

			&.mouseleave {
				overflow-y: hidden;
			}
		}

		.el-menu {
			border-right: none;
		}
	}

	@media (min-width: 768px) {
		::-webkit-scrollbar-thumb:vertical {
			background-color: rgba(222, 223, 224, 1);
		}
	}
</style>



